<template>
	<view class="wrapper">
		<image src="http://qp9mr1fhs.hn-bkt.clouddn.com/img/my-bg.jpg" class="bg"></image>
		<view class="content" :style="{height:bgHeight}">
			<view class="whitebox">
				<view class="utitlebox">
					<view class="utitleblock"></view>
					<view class="utitle">入驻申请</view>
				</view>
				<u-form style="margin-left: 20upx;margin-top: 20upx;">
					<u-form-item label="商户名称:">
						<u-input placeholder="请输入商户或公司名称" v-model="sname" />
					</u-form-item>
					<u-form-item label="商户地址:">
						<u-input placeholder="请输入商户或公司的详细地址" v-model="sdz" />
					</u-form-item>
					<u-form-item label="商家姓名:">
						<u-input placeholder="请输入您的姓名" v-model="name" />
					</u-form-item>
					<u-form-item label="联系方式:">
						<u-input placeholder="请输入您的手机号码" v-model="snum" type="number" maxlength="11"/>
					</u-form-item>
				</u-form>
				<view class="utitlebox" style="margin-top: 30upx; margin-bottom: 20upx;">
					<view class="utitleblock"></view>
					<view class="utitle">上传企业证书</view>
						<view class="illustration" @click="ck">查看例图</view>
				</view>
				<u-mask :show="cklt" @click="qx">
				
					<view :class="cklt==false ? 'noseeill':'seeill'">
						<image src="http://qp9mr1fhs.hn-bkt.clouddn.com/img/yyzz.jpg" class="ltimg"></image>
					</view>
				</u-mask>
				<text class="scts">
					(请上传食品经营许可证、营业执照等)
				</text>
				<view class="scbox">
					<u-upload ref="uUpload" :action="action" :auto-upload="false" max-count="6" :model="model"></u-upload>
				</view>
			</view>
			<view class="btbox" style="margin-top: 100upx;">
				<view class="bt1" @click="btback">返回</view>
				<view class="bt2" @click="btconfirm">申请</view>
			</view>
		</view>

	</view>

</template>

<script>
	export default {
		data() {
			return {
				cklt:false,
				bgHeight: '',
				//表单
				sname: '',
				sdz: '',
				name: '',
				snum: '',
				//表单
				action:'',//后台接口地址
				
			}
		},
		methods: {
			ck() {
				this.cklt = true
			},
			qx() {
				this.cklt = false
			},
			btback() {
				uni.switchTab({
					url: '../my/my'
				})
			},
			btconfirm(){
				let re = /[^\u4e00-\u9fa5]/;
				let _this=this;
				if(this.sname == '' || this.sdz == '' || this.name == '' || this.snum == '' ){
					uni.showToast({
						title:'请将信息输入完整',
						icon:'none'
					})
				}
				else if(this.name.length >4){
					uni.showToast({
						title:'您的姓名输入有误',
						icon:'none'
					})
				}
				else if (re.test(this.name)){
					uni.showToast({
						title:'您的姓名输入有误',
						icon:'none'
					})
				}
				     
				else if(this.name.length >4 || this.name.length <2){
					uni.showToast({
						title:'商家姓名输入有误',
						icon:'none'
					})
				}
				else if(this.snum.substr(0,1) != 1){
					uni.showToast({
						title:'手机号码有误',
						icon:'none'
					})
				}
				else if(this.snum.length >11 || this.snum.length <11){				
					uni.showToast({
						title:'手机号码有误',
						icon:'none'
					})
				}	
				else if(this.$refs.uUpload.lists.length == 0 ){
					uni.showToast({
						title:'请选择图片',
						icon:'none'
					})					
				}else{
					
					uni.showModal({
						title:"申请成功",
						content:"将在3个工作日内给予您答复\n请多关注公众号推送\n",
						showCancel:false,
						confirmColor:'#0ABB9A',
						success: (res) => {
							if(res.confirm){
								uni.switchTab({
									url:"../my/my"
								})
							}
						}
					})
					
				}
			}
		},
		onReady() {
			const _this = this;
			uni.getSystemInfo({
				success(res) {
					let allheight = res.windowHeight
					let shopWidth = res.windowWidth
					// 阅读框高度
					_this.bgHeight = res.windowHeight - 80 + "px";

				}
			})
		},
	}
</script>

<style>
	page {
		background-color: transparent;
	}

	.bg {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: -1;
	}

	.scbox {
		margin-top: 20upx;
		box-shadow: 0 0 12upx #C0C4CC;
		border-radius: 20upx;
		padding: 15upx 0upx 15upx 9upx;
	}

	.scts {
		color: #C0C0C0;
		font-size: 20upx;
		margin-left: 15upx;
	}
	.noseeill {
		display: none;
	}
	
	.seeill {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100%;
	}
	.illustration {
		width: 130upx;
		height: 50upx;
		background-color: #0ABB9A;
		margin-left: 20upx;
		color: white;
		font-size: 20upx;
		line-height: 50upx;
		text-align: center;
		border-radius: 15upx;
		margin-top: -5upx;
	}
	.ltimg {
		height: 830upx;
		width: 640upx;
	}
</style>
